.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #750060;
    padding: 1rem 2rem;
    margin-bottom: auto;
    color: white;
    box-shadow: 0 4px 8px rgba(0,0,0,0.5);
  }
  .logo {
    font-size: 1.5rem;
    font-weight: bold;
  }
  .menu {
    list-style: none;
    display: flex;
    gap: 1.5rem;
    margin: 0;
    padding: 0;
  }
  .menu-item {
    cursor: pointer;
    transition: color 0.3s;
    background-color: #f7dbf8;
    border: none;
    padding: 0.8rem 1.5rem;
    border-radius: 26px;
    font-size: 1rem;
    font-weight: bold;
    color: #000000;
    cursor: pointer;
    transition: all 0.3s ease-in-out;
  }
  .menu-item:hover {
    background-color: #df48c1;
    color: rgb(0, 0, 0);
    transform: scale(1.1);
  }
  .header h5 {
    padding-top: 10%;
    text-align: center;
   font-family: serif;
    font-size: 1rem;
    margin-bottom: 0rem;
    line-height: 0.1rem;
  }
  .header h1 {
    font-size: 5rem;
    font-family: initial;
    text-align: center;
    margin: 0rem;
  }
  .header p {
    font-size: 1.2rem;
    text-align: center;
    line-height: 1.5;
    font-family: serif;
  }
   .styled-paragraph {
    border: 2px solid #e60dc9;  
    background-color: #f0ddee;  
    border-radius: 8px;  
    margin: 2px;
    margin-left: 20px;
    padding: 20px;
    width: fit-content;
   } 
   